<template>
  <div class="container py-4">
    <div class="row">
      <div class="col">
        <h4>Apresente a sua vaga para milhares de profissionais e de graça</h4>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col">
        <label class="form-label">Título da Vaga</label>
        <input type="text" class="form-control" v-model="titulo">
        <div class="form-text">Por exemplo: Programador JavaScript e VueJS.</div>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col">
        <label class="form-label">Descrição</label>
        <textarea type="text" class="form-control" v-model="descricao"></textarea>
        <div class="form-text">Informe os detalhes da vaga.</div>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col">
        <label class="form-label">Salário</label>
        <input type="number" class="form-control" v-model="salario">
        <div class="form-text">Informe o salário.</div>
      </div>

      <div class="col">
        <label class="form-label">Nível</label>
        <select class="form-select" v-model="nivel">
          <option value="" disabled>--Selecione</option>
          <option value="1">Júnior</option>
          <option value="2">Pleno</option>
          <option value="3">Sênior</option>
        </select>
        <div class="form-text">Informe o nível do profissional.</div>
      </div>

      <div class="col">
        <label class="form-label">Modalidade</label>
        <select class="form-select" v-model="modalidade">
          <option value="" disabled>--Selecione</option>
          <option value="1">Home Office</option>
          <option value="2">Presencial</option>
        </select>
        <div class="form-text">Informe onde as atividades serão realizadas.</div>
      </div>

      <div class="col">
        <label class="form-label">Tipo</label>
        <select class="form-select" v-model="tipo">
          <option value="" disabled>--Selecione</option>
          <option value="1">CLT</option>
          <option value="2">PJ</option>
        </select>
        <div class="form-text">Informe o tipo de contratação.</div>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col">
        <button type="submit" class="btn btn-primary" @click="salvarVaga()">Cadastrar</button>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'PublicarVaga',
  data: () => ({
    titulo: '',
    descricao: '',
    salario: '',
    modalidade: '',
    tipo: ''
  }),
  methods: {
    salvarVaga() {

      let tempoDecorrido = Date.now()
      let dataAtual = new Date(tempoDecorrido)

      let vagas = JSON.parse(localStorage.getItem('vagas'))
      //console.log(vagas)

      if(!vagas) vagas = []
      
      vagas.push({
        titulo: this.titulo,
        descricao: this.descricao,
        salario: this.salario,
        nivel: this.nivel,
        modalidade: this.modalidade,
        tipo: this.tipo,
        publicacao: dataAtual.toISOString() //data e hora no time zone UTC
      })
      
      if(this.validaFromulario()) {
        localStorage.setItem('vagas', JSON.stringify(vagas))
              this.emitter.emit('alerta', {
                tipo: 'sucesso',
                titulo: `A vaga ${this.titulo} foi cadastrada com sucesso!`
              })
              this.resetaFormularioCadastroVaga()
      } else {
        this.emitter.emit('alerta', {
                tipo: 'erro',
                titulo: `Por favor, Preencha todos os campos do formulário para realizar o cadastro da vaga!`
              })
      }      
    },
    resetaFormularioCadastroVaga() {
      this.titulo = '',
      this.descricao = '',
      this.salario = '',
      this.nivel = '',
      this.modalidade = '',
      this.tipo = ''
    },
    validaFromulario() {
      let valido = true;

      if(this.titulo === '') valido = false
      if(this.descricao === '') valido = false
      if(this.salario === '') valido = false
      if(this.nivel === '') valido = false
      if(this.modalidade === '') valido = false
      if(this.tipo === '') valido = false

      return valido
    }
  }
}
</script>

